import { Component, ViewChild } from '@angular/core';
import { NavController} from 'ionic-angular';
import { Http } from '@angular/http';
import { LoadingController, Slides  } from 'ionic-angular';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})

export class HomePage {

  public search:any;
  public result:any;
  icons: string;
  @ViewChild(Slides) slides: Slides;
  //
  ionViewWillEnter() {
    this.slides.startAutoplay();
  }
  ionViewWillLeave() {
    this.slides.stopAutoplay();
  }
  constructor(private http: Http, public navCtrl: NavController, public loadingCtrl: LoadingController) {
    this.search="";
    this.icons = 'search';

  }

  getSearch(e){
   
    if(e.keyCode==13){
      this.dosearch();
    }

  }

  dosearch(){
    if(this.search==""){
      let msg = this.loadingCtrl.create({
        content: ''//loading
      });
      msg.present();
      setTimeout(()=>{ msg.dismiss(); },500);
      return;
    }
    let loading = this.loadingCtrl.create({
      content: ''//loading
    });

    loading.present();
    var url = "http://998xp.vicp.net:8000/api?q="+this.search
    var that = this;
    this.http.get(url).subscribe(
      function(data){
        that.result = data['_body'];
        that.search = "";
        this.result = that.result;
        loading.dismiss();
      },function(err){
        loading.dismiss();
      }
    )
  }
}


